body {
  padding-top: 60px;
  /* background-color: #000;
    color: #fff; */
  border-collapse: separate;
}

.nav {
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(13px);
  display: flex;
  height: 50px;
  margin: 10px;
  border-radius: 10px;
  box-sizing: border-box;
  position: fixed;
  width: calc(90% - 20px);
  top: 0;
  align-items: center;
  padding-left: 10px;
  z-index: 5;
  left: 10%;
}

.nav-back {
  left: 0;
  width: calc(10% - 10px);
  font-size: 1.5em;
  text-align: center;
}

.tac {
  text-align: center;
}

.shadow {
  box-shadow: 0 0 5px #ccc;
}

.m20 {
  margin: 20px;
}

.shadow-move {
  transition: transform 0.5s, box-shadow 0.5s;
  transform: translateY(3px);
}
.shadow-move:hover {
  box-shadow: 0 0 8px #ccc;
  transform: translateY(0);
}

.shadow-move:active {
  box-shadow: 0 0 6px #ccc;
  transform: translateX(2);
}

.nav-link,
.nav-brand {
  display: flex;
}

.nav-brand {
  width: 40%;
}

a {
  text-decoration: none;
  list-style: none;
  color: black;
}

.nav-link {
  width: 60%;
  justify-content: space-evenly;
}

.nav-link p {
  font-size: 20px;
}

.main-bg {
  height: 300px;
  background-image: url("/public/bg.png");
  background-size: cover;
  background-position: center ;
  background-repeat: no-repeat;
}

.fit100 {
  width: calc(100-30px);
}

.shoelist {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.shoelist h4,
.shoelist p,
.shoelist img {
  text-align: center;
  margin: 10px;
}

.shoelist img {
  width: auto;
  max-height: 150px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.detail-title {
  line-height: 50px;
}

.detail-p {
  line-height: 30px;
}

.detail-img {
  width: 80%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 40%;
  max-width: 600px;
  transform: scale(0);
  transition: all 0.5s;

  transition-timing-function: cubic-bezier(0.57, 0.28, 0, 1.79);
}

.detail-img-ani {
  transform: scale(1);
}

.detail-card h3 {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 15;
}

.detail-card p {
  text-align: center;
  margin: 15px;
  display: block;
}

.btn {
  background-color: #fff;
  width: 200px;
  align-items: center;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.menu {
  margin-right: 20px;
}

.nav-back {
  margin-right: 30px;
}

.cart {
  display: table;
  width: calc(100% - 20px);
  table-layout: fixed;
  border-collapse: separate;
}

.cart thead {
  text-align: left;
}

.cart .short-table {
  width: 10%;
}

.cart .long-table {
  width: 70%;
}

.cart th {
  padding-bottom: 10px;
}

.alert {
  background-color: #ffe3e3;
}

.control-btn {
  padding: 5px;
  margin: 3px;
  border-radius: 10px;
}